.reviews-container {
  width: fit-content;
  background-color: rgba(255, 255, 255, 0.774);
  border-radius: 20px;
  margin: auto;
  .review {
    font-family: caveat, serif;
    font-size: rem(30px);
    text-align: left;
    .reviewer-name {
      font-family: montserrat, sans-serif;
      font-size: rem(20px);
    }
  }

  h1 {
    font-family: montserrat, sans-serif;
    color: $clr1;
    font-size: rem(20px);
  }
}
