.toy-container {
  // background-color: rgba($clr1, 0.5);
  // background-color: rgba(lighten($clr3, 20%), 0.9);
  background-color: $clr8;
  background-color: #fff1eb;
  width: 250px;
  line-height: 4;
  text-align: center;
  color: black;
  border-radius: 20px;
  border: 3px solid $clr1;
  transition: 0.2s;
  &:hover {
    // background-color: rgba($clr5, 0.9);
    // background-color: darken(rgba($clr8, 0.8), 5%);
    background-color: darken(rgba(#fff1eb, 0.8), 5%);
    cursor: pointer;
  }
}

.chart-link {
  background-color: $clr6;
  width: fit-content;
  height: 50px;
  margin: 20px auto;
  list-style: none;
  text-decoration: none;
  text-transform: none;
  color: $clr2;
  border: 2px solid $clr1;
  padding: 5px;
  border-radius: 5px;
  transition: background-color 0.5s;
  &:hover {
    background-color: darken($clr6, 10%);
  }
}

.instock {
  color: green;
}
.outstock {
  color: red;
}
