.app-header {
  background-color: darken($clr5, 20%);
  background-color: lighten($clr3, 15%);
  background-color: $clr9;
  background-color: rgba($clr9, 0.8);
  height: 120px;
  line-height: 5;

  .header-content {
    max-width: 90%;
    margin: auto;
    justify-content: space-between;
    .logo {
      font-size: rem(50px);
      color: $clr2;
      line-height: 2.3;
    }
    & nav {
      width: 300px;
      display: flex;
      justify-content: space-between;
    }
  }
  .header-link {
    color: lighten($clr2, 5%);
    font-size: 1.5rem;
    margin-inline: 7px;
    font-family: montserrat;
    &:hover {
      color: darken($clr2, 20%);
    }
  }
}
